import { Component, OnInit } from '@angular/core';
import { DialogService } from 'src/app/services/dialog/dialog-v2.service';
import { HttpError } from '../../../shared/classes/httpError.class';
import { MobileService } from '../../../services/mobile.service';
import { ScannerComponent } from '../../../shared/components/common/scanner/scanner.component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-descarga-entrada',
  templateUrl: './descarga-entrada.component.html',
  styleUrls: ['./descarga-entrada.component.sass']
})
export class DescargaEntradaComponent implements OnInit {

  entradas = []
  entradasDia = []

  constructor(
    private ds: DialogService,
    private ms: MobileService,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.getReferencias();
  }

  async getReferencias(){
    try{

      let entradas = <any>await this.ms.getEntradasDescargas();
      this.entradas    = entradas?.entradas.map(e=>({
        ...e,
        Evento: e.Estatus.idEstatus === 2 ? { fecha: e.fechaArribo } : e.Eventos?.length ? e.Eventos[0] : {}
      }));
      this.entradasDia = entradas?.entradasDia.map(e=>({
        ...e,
        Evento: e?.Eventos[0]
      }));

    }catch(err){
      console.log(err);
      let error = new HttpError(err);
      this.ds.showSnack( error.message || 'Ocurrio un problema','','error' );
    }
  }

  async handleQr( entrada ){
    try{

      if( entrada?.Estatus.idEstatus === 2 ){
        let qrEntrada = await this.ds.openModalFullComponent( ScannerComponent );

        if( !qrEntrada ) return;
        if( qrEntrada !== entrada.entrada ) return this.ds.showSnack('El qr no pertenece a la referencia seleccionada.','','error');

        let updateEntrada = await this.ms.patchEntrada(entrada.idEntrada,{
          accion:'INI-DESCARGA',
        });

        entrada.Estatus = updateEntrada.Estatus;
        entrada.Evento = updateEntrada.Evento;
      }

      this.router.navigate(['herramientas','descarga','detalle',entrada.idEntrada])


    }catch(err){
      let error = new HttpError(err);
      this.ds.showSnack( error.message || 'Ocurrio un problema','','error' );
    }
  }

}
