import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CargaEntradaComponent } from './carga-entrada/carga-entrada.component';
import { DescargaEntradaComponent } from './descarga-entrada/descarga-entrada.component';
import { DecargaDetalleComponent } from './decarga-detalle/decarga-detalle.component';

const routes: Routes = [
  { path:'carga',component: CargaEntradaComponent },
  { path:'descarga/detalle/:idEntrada',component: DecargaDetalleComponent },
  { path:'descarga',component: DescargaEntradaComponent },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HerramientasRoutingModule { }
