import { Injectable } from '@angular/core';
import { UsuarioService } from './usuario/usuario.service';
import { HttpClient } from '@angular/common/http';
import { URL } from '../config/url.services';

@Injectable({
  providedIn: 'root'
})
export class MobileService {

  constructor(
    private _us: UsuarioService,
    private _http: HttpClient
  ) { }

  async getEntradasDescargas(){
    let response = <any>await this._http.get(`${URL}/mobile/entrada/descarga`).toPromise();
    return response;
  }

  async getEntradasCargadas(){
    let response = <any>await this._http.get(`${URL}/mobile/entrada/carga`).toPromise();
    return response;
  }

  async patchEntrada(entrada,data){
    let response = <any>await this._http.post(`${URL}/mobile/entrada/${entrada}`,data).toPromise();
    return response;
  }
}
