import { Component, OnInit } from '@angular/core';
import { DialogService } from 'src/app/services/dialog/dialog-v2.service';

@Component({
  selector: 'app-decarga-detalle',
  templateUrl: './decarga-detalle.component.html',
  styleUrls: ['./decarga-detalle.component.sass']
})
export class DecargaDetalleComponent implements OnInit {

  aplicaOSD:boolean = false;
  archivos:any = [];
  isLoadingArchivos: boolean =false;

  fotosArribo  = [];
  fotosPaletas = [];

  constructor(
    private _ds: DialogService
  ) { }

  ngOnInit(): void {
  }

  getFile(idTipoDocumento){
    return this.archivos.find(archivo=> archivo.idTipoDocumento === idTipoDocumento)
      ? [this.archivos.find(archivo=> archivo.idTipoDocumento === idTipoDocumento)]
      : []
  }

  async handleChangeFilesArribo({ archivos,accion }){
    try{
      let archivo = {...archivos[0]};
      if(accion === 'deleted'){
        this.fotosArribo = this.fotosArribo.filter(i=>i.id !== archivo.id );
      }

      if(accion === 'change'){
        this.fotosArribo = archivos
      }
    }catch(err){
      console.log(err);
    }
  }

  async handleChangeFilesPaletas({ archivos,accion }){
    try{
      let archivo = {...archivos[0]};
      if(accion === 'deleted'){
        this.fotosPaletas = this.fotosPaletas.filter(i=>i.id !== archivo.id );
      }

      if(accion === 'change'){
        this.fotosPaletas = archivos
      }
    }catch(err){
      console.log(err);
    }
  }
}
